-width: 2px;\n// }\n\n@import './variables.scss';\n@import './mixins.scss';\n\n:root {\n  --check-theme-url: url('../icon/icon/theme-background/check-light-theme.svg');\n  --expand-more-theme-url: url('../icon/icon/theme-background/expand-more-light-theme.svg');\n  --search-theme-url: url('../icon/icon/theme-background/search-light-theme.svg');\n}\n\nhtml[data-supports-dark-theme=true][data-prefers-color-scheme=\"dark\"] {\n  --check-theme-url: url('../icon/icon/theme-background/check-dark-theme.svg');\n  --expand-more-theme-url: url('../icon/icon/theme-background/expand-more-dark-theme.svg');\n  --search-theme-url: url('../icon/icon/theme-background/search-dark-theme.svg');\n}\n\n\n.flex {\n  display: flex;\n}\n\n.flex-grow-column {\n  composes: flex;\n  flex: 1 0 auto;\n  flex-direction: column;\n}\n\n.stretch {\n  composes: flex;\n  justify-content: space-between;\n}\n\n.flex-end {\n  composes: flex;\n  justify-content: flex-end;\n}\n\n.vertically-center {\n  composes: flex;\n  align-items: center;\n}\n\n.align-baselines {\n  composes: flex;\n  align-items: baseline;\n}\n\n// Corner radii\n.rounded-none {\n  border-radius: 0;\n}\n\n.rounded-sm {\n  border-radius: 2px;\n}\n\n.rounded-m {\n  border-radius: 4px;\n}\n\n.rounded-lg {\n  border-radius: 6px;\n}\n\n.bottom-divider {\n  box-shadow: 0 1px 0 0 var(--box-shadow);\n\n  // Edge: High contrast mode\n  @media screen and (-ms-high-contrast: active) {\n    box-shadow: 0 1px 0 0 buttonText;\n  }\n}\n\n.bottom-divider-inset {\n  box-shadow: inset 0 -1px 0 0 var(--box-shadow);\n\n  // Edge: High contrast mode\n  @media screen and (-ms-high-contrast: active) {\n    box-shadow: 0 1px 0 0 buttonText;\n  }\n}\n\n.top-divider {\n  box-shadow: 0 -1px 0 0 var(--box-shadow);\n\n  // Edge: High contrast mode\n  @media screen and (-ms-high-contrast: active) {\n    box-shadow: 0 -1px 0 0 buttonText;\n  }\n}\n\n.top-divider-inset {\n  box-shadow: inset 0 1px 0 0 var(--box-shadow);\n\n  // Edge: High contrast mode\n  @media screen and (-ms-high-contrast: active) {\n    box-shadow: inset 0 1px 0 0 buttonText;\n  }\n}\n\n.unbutton {\n  background: transparent;\n  border: none;\n  box-shadow: none;\n  cursor: pointer;\n  padding: 0;\n  margin: 0;\n  font-size: inherit;\n  font-weight: unset;\n  font-family: inherit;\n  color: unset;\n  text-align: unset;\n}\n\n.icon-background {\n  background-repeat: no-repeat;\n  background-size: contain;\n}\n\n.icon-before {\n  &::before {\n    @extend .icon-background;\n    background-position: center center;\n    content: '';\n    display: inline-block;\n    height: 20px;\n    width: 20px;\n  }\n}\n\n// Icon\n$icons: 'check', 'expand-more', 'search';\n\n@each $icon in $icons {\n  .icon-background-#{$icon} {\n    @extend .icon-background;\n    background-image: var(--#{$icon}-theme-url);\n  }\n\n  .icon-before-#{$icon} {\n    &::before {\n      @extend .icon-before;\n      background-image: var(--#{$icon}-theme-url);\n    }\n  }\n}\n\n.form-message {\n  margin-top: $space-xxs;\n  font-size: $body-s;\n  line-height: 15px;\n\n  a,\n  a:hover,\n  a:active,\n  a:visited {\n    color: inherit;\n    text-decoration: underline;\n  }\n}\n\n.form-error-message {\n  composes: form-message;\n  color: $error-text;\n}\n\n.form-info-message {\n  composes: form-message;\n  color: $secondary-text;\n}\n\n.form-success-message {\n  composes: form-message;\n  color: $success-text;\n}\n\n.form-label {\n  display: block;\n  margin-bottom: 5px;\n  font-size: $body-m;\n  line-height: 20px;\n}\n\n// Other entry sizes defined after the space classes.\n.entry {\n  @include entry;\n  box-sizing: border-box;\n  color: $primary-text;\n  width: 100%;\n  border-color: $gray-40;\n\n  &::placeholder {\n    color: var(--secondary-text);\n\n    // Edge: High contrast mode\n    @media screen and (-ms-high-contrast: active) {\n      color: windowText;\n    }\n  }\n\n  &[disabled],\n  &[readonly] {\n    &::placeholder {\n      color: $hint-text;\n\n      // Edge: High contrast modes\n      @media screen and (-ms-high-contrast: active) {\n        color: grayText;\n      }\n    }\n  }\n}\n\n@keyframes skeleton-pulse {\n  0% {\n    opacity: 0.4;\n    animation-timing-function: $ease-out-cubic;\n  }\n\n  50% {\n    opacity: 0.8;\n    animation-timing-function: $ease-in-cubic;\n  }\n\n  100% {\n    opacity: 0.4;\n  }\n}\n\n.skeleton-pulse {\n  animation: skeleton-pulse 1.5s linear infinite;\n}\n\n.off-screen {\n  position: absolute;\n  top: -9999px;\n  left: -9999px;\n  pointer-events: none;\n  opacity: 0;\n  z-index: -1;\n}\n\n// Spaces\n.padding-xxs {\n  padding: $space-xxs;\n}\n\n.padding-xxs-bordered {\n  padding: $space-xxs - 1px;\n  border-width: 1px;\n}\n\n.padding-xs {\n  padding: $space-xs;\n}\n\n.padding-xs-bordered {\n  padding: $space-xs - 1px;\n  border-width: 1px;\n}\n\n.padding-s {\n  padding: $space-s;\n}\n\n.padding-s-bordered {\n  padding: $space-s - 1px;\n  border-width: 1px;\n}\n\n.padding-m {\n  padding: $space-m;\n}\n\n.padding-m-bordered {\n  padding: $space-m - 1px;\n  border-width: 1px;\n}\n\n.padding-l {\n  padding: $space-l;\n}\n\n.padding-l-bordered {\n  padding: $space-l - 1px;\n  border-width: 1px;\n}\n\n.padding-xl {\n  padding: $space-xl;\n}\n\n.padding-xl-bordered {\n  padding: $space-xl - 1px;\n  border-width: 1px;\n}\n\n.padding-xxl {\n  padding: $space-xxl;\n}\n\n.padding-xxl-bordered {\n  padding: $space-xxl - 1px;\n  border-width: 1px;\n}\n\n.padding-squish-xxs {\n  padding: $space-squish-xxs;\n}\n\n.padding-squish-xxs-bordered {\n  padding: $space-squish-xxs-v - 1px $space-squish-xxs-h - 1px;\n  border-width: 1px;\n}\n\n.padding-squish-xs {\n  padding: $space-squish-xs;\n}\n\n.padding-squish-xs-bordered {\n  padding: $space-squish-xs-v - 1px $space-squish-xs-h - 1px;\n  border-width: 1px;\n}\n\n.padding-squish-s {\n  padding: $space-squish-s;\n}\n\n.padding-squish-s-bordered {\n  padding: $space-squish-s-v - 1px $space-squish-s-h - 1px;\n  border-width: 1px;\n}\n\n.padding-squish-m {\n  padding: $space-squish-m;\n}\n\n.pa